import React, { useState } from 'react'
import { useSelector } from 'react-redux';
import { Stage, Layer, Rect, Text, Circle, Line, Group } from 'react-konva';



const TreeLevelCircleLineComponent = () => {
    const konvaInfo = useSelector( state => state.KonvaData )
    // const [ movableNodesXPosition, setMovableNodesXPosition ] = useState( konvaInfo.movableNodesXPosition )
    console.log( konvaInfo.xRatio )

    return (
        <>
            <Group>
                <Line
                    points={ [ konvaInfo.points.x1, konvaInfo.points.y1, konvaInfo.points.x2, konvaInfo.points.y2 ] }
                    stroke="black"
                />
                <Circle x={ konvaInfo.points.x1 } y={ konvaInfo.points.y1 } radius={ konvaInfo.circleRadius } fill={ 'green' } />
                <Circle x={ konvaInfo.points.x2 } y={ konvaInfo.points.y2 } radius={ konvaInfo.circleRadius } fill={ 'red' } />
                <Circle x={
                    (
                        // ratio.konvaInfo.points.x2 + konvaInfo.points.x1
                        ( ( konvaInfo.xRatio * konvaInfo.points.x2 + konvaInfo.points.x1 ) / ( 1 + konvaInfo.xRatio ) ) 
                    )
                }
                    y=
                    { ( ( konvaInfo.xRatio * konvaInfo.points.y2 + konvaInfo.points.y1 ) / ( 1 + konvaInfo.xRatio ) ) }
                    radius={ konvaInfo.circleRadius } fill={ 'white' }
                    stroke={ 'black' }
                    strokeWidth={ 5 }
                />
            </Group>
        </>
    )
}
export default ( TreeLevelCircleLineComponent );
