import React, { useState } from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { setIncreasingNodeOneXYPosition, setIncreasingNodeTwoXYPosition, setDecreasingNodeOneXYPosition, setDecreasingNodeTwoXYPosition, setMovableNodesX, setMovableNodesY } from '../features/konvaReducer'
import { Button } from 'react-bootstrap'


const ControlComponent = () => {
    const konvaInfo = useSelector( state => state.KonvaData )
    const dispatch = useDispatch()
    return (
        <div
            className="m-auto w-50"
            style={ {
                position: 'absolute',
                top: '0'
            } }
        >
            <Button
                className="mt-2 me-2 mr-2 btn-success"
                onClick={ ( e ) => {

                    dispatch( setIncreasingNodeOneXYPosition( 20 ) )
                } }
            >+</Button>
            <Button
                className="mt-2 me-2 mr-2 btn-success"
                onClick={ ( e ) => {
                    dispatch( setDecreasingNodeOneXYPosition( 20 ) )
                } }
            >-</Button>

            <Button
                className="mt-2 me-2 mr-2 btn-danger"
                onClick={ ( e ) => {
                    dispatch( setIncreasingNodeTwoXYPosition( 20 ) )
                } }
            >+</Button>

            <Button
                className="mt-2 me-2 mr-2 btn-danger"
                onClick={ ( e ) => {
                    dispatch( setDecreasingNodeTwoXYPosition( 20 ) )
                } }
            >-</Button>
            <Button
                className="mt-2 me-2 mr-2 btn-dark"
                onClick={ ( e ) => {
                    dispatch( setMovableNodesX(20) )
                } }
            >Holes</Button>

        </div>
    )
}

export default ControlComponent
